import React, { useState } from "react";

interface Option {
  title: string;
  link: () => void;
  suboptions?: ReadonlyArray<Option>;
  isActive?: boolean;
}

interface Props {
  options: ReadonlyArray<Option>;
}

export const NavigationBar = ({options}: Props) => {
  const [currentOptions, setActive] = useState(options);

  return <div style={styles.container}>
    {currentOptions.map((option: Option) => 
      <div key={option.title} style={styles.options}
        onMouseEnter={() => option.suboptions && setActive(options.map((value) => value.title === option.title ? ({...option, isActive: true}) : value))}
        onMouseLeave={() => option.suboptions && setActive(options.map((value) => value.title === option.title ? ({...option, isActive: false}) : value))}>
        <span style={styles.option} onClick={option.link}>{option.title}</span>
        {option.isActive && 
          <div style={styles.suboptions}>{option.suboptions?.map((suboption) =>
            <span key={suboption.title} style={styles.suboption} onClick={suboption.link}>{suboption.title}</span>)}
          </div>}
      </div>
    )}
  </div>;
}
const styles = {
  container: {
    display: "flex",
    flexDirection: "row",
    marginTop: "1%",
    fontSize: "large",
    fontFamily: "circular",
    borderBottom: "outset",
    borderColor: "#e0452c"
  },
  options: {
    display: "flex",
    flexDirection: "column",
    margin: "0% 1%",
    width: "100%"
  },
  option: {
    margin: "0% 2%",
    cursor: "pointer"
  },
  suboptions: {
    display: "flex",
    flexDirection: "column",
    position: "absolute",
    backgroundColor: "white",
    padding: "1%",
    zIndex: 1,
    marginTop: "22px",
    borderBottom: "outset #e0452c",
    borderRight: "outset #e0452c",
    borderLeft: "outset #e0452c"
  },
  suboption: {
    cursor: "pointer"
  }
} as Record<string, React.CSSProperties>;