import React, { useEffect, useState } from 'react';
import { About } from './pages/about';
import { Homepage } from './pages/homepage';
import { Gallery } from './pages/gallery';
import { Suggestions } from './pages/suggestions';
import { NavigationBar } from './components/navigation-bar';
import { UnderConstruction } from './components/under-construction';
import { Events } from './pages/events';
import results from "./data.json";
import { Modal, ModalProps } from './components/modal';

enum Page {
  About,
  Events,
  Gallery,
  Homepage,
  Suggestions,
  UnderConstruction
}

const App: React.FC = () => {
  const [data, setData] = useState<Record<string, any>>(results);
  const [page, setPage] = useState<Page>(Page.Homepage);
  const [modalProps, setModalProps] = useState<ModalProps | null>(null);

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    try {
      const response = await fetch(
        "https://3375-2405-201-1007-143-89ce-bf56-d6bf-bc56.ngrok-free.app/data",
        {headers: new Headers({
          "ngrok-skip-browser-warning": "69420",
        })}
      );
      const data = await response.json();
      console.log(data);
      setData(data);
    } catch (e) {
      console.log(e);
    }
  };

  const navigationOptions = [
    {
      title: "Home",
      link: () => setPage(Page.Homepage),
    },
    {
      title: "Events",
      link: () => setPage(Page.Events),
      suboptions: [
        { title: "Upcoming Events", link: () => setPage(Page.UnderConstruction) }, { title: "Past Events", link: () => setPage(Page.UnderConstruction) }
      ]
    },
    {
      title: "Gallery",
      link: () => setPage(Page.UnderConstruction),
      suboptions: [
        { title: "Photos", link: () => setPage(Page.UnderConstruction) }, { title: "Videos", link: () => setPage(Page.UnderConstruction) }
      ]
    },
    {
      title: "Suggestions",
      link: () => setPage(Page.Suggestions)
    },
    {
      title: "About Us",
      link: () => setPage(Page.About),
    }
]

  return (
    <div>
      <div style={styles.header}>
        <div style={{display: "flex", justifyContent: "space-between"}}>
          <img src={`${process.env.PUBLIC_URL}/logo.jpeg`} style={styles.logo} alt={"logo"}/>
        </div>
        <NavigationBar options={navigationOptions} />
      </div>
      <div style={styles.page}>
        {page === Page.Homepage &&
          (!!data && <Homepage setModalProps={setModalProps} events={data.Events} testimonials={data.Testimonials} />)}
        {page === Page.Events &&
          <Events events={data.Events}/>}
        {page === Page.About &&
          <About text={data.AboutUs}/>}
        {page === Page.Gallery &&
          <Gallery />}
        {page === Page.Suggestions &&
          <Suggestions text={data.Suggestions}/>}
        {page === Page.UnderConstruction &&
          <UnderConstruction />}  
      </div>
      <div>

      </div>
      {modalProps && 
        <Modal 
          onClose={modalProps.onClose}
          title={modalProps.title} 
          children={modalProps.children} />}
    </div>
  );
};

const styles = {
  header: {
    padding: "0.5% 1%"
  },
  navigationBar: {
    display: "flex",
    fontSize: "large",
    fontFamily: "circular",
    marginTop: "1%"
  },
  navigationLink: {
    margin: "0% 1%",
    cursor: "pointer"
  },
  logo: {
    height: "8%",
    width: "8%",
    cursor: "pointer"
  },
  banner: {
    height: "8%",
    width: "8%",
  },
  page: {
    display: "flex",
    flexDirection: "column",
    height: "75vh"
  },
  footer: {

  }
} as Record<string, React.CSSProperties>;

export default App;
